import fetchobj from '@/utils/fetch'
let fetch = fetchobj.fetch;
/**
 * @description 版本控制相关接口
 * @function Version
 * @param  {type} export const InspectionNotice {description}
 * @return {type} {description}
 */
export const Version = {
  /**
   * @description 新增版本
   * @function addVersion
   */
  addVersion: {
    url: '/api/v1/installNavAll',
    baseUrl: fetchobj.baseURL,
  },
  /**
   * @description 删除版本号
   * @function deleteVersion
   */
  deleteVersion(data) {
    return fetch('delete', '/api/v1/deleteNavAll', data)
  },
  /**
   * @description 修改版本号
   * @function changeVersion
   */
  changeVersion: {
    url: '/api/v1/updateNavAll',
    baseUrl: fetchobj.baseURL,
  },
  /**
   * @description 获取版本号
   * @function getVersion
   */
  getVersion(data) {
    return fetch('get', '/api/v1/selectNavAll', data)
  },
  /**
   * @description 版本回退
   * @function versionBack
   */
  versionBack(data) {
    return fetch('get', '/api/v1/servers/back', data)
  },
  /**
   * @description 版本上传
   * @function versionUpload
   */
  versionUpload(data) {
    return fetch('get', '/api/v1/servers/upload', data)
  },

  /**
   * @description 主机删除
   * @function serviceDelete
   */
  serviceDelete(data) {
    return fetch('delete', '/api/v1/servers/delete', data)
  },
  /**
   * @description 主机新增
   * @function serviceInstall
   */
  serviceInstall(data) {
    return fetch('post', '/api/v1/servers/install', data)
  },
  /**
   * @description  获取所有主机
   * @function serviceGet
   */
  serviceGet(data) {
    return fetch('get', '/api/v1/servers/select/all', data)
  },
  /**
   * @description 更新主机  
   * @function serviceUpdate
   */
  serviceUpdate(data) {
    return fetch('post', '/api/v1/servers/update', data)
  }
  

  

  

  
}
