<style lang="scss" scoped>
.dialog_pram {
  margin-bottom: 10px;
  display: block;
}
</style>

<template>
  <div class="app-container">
    <el-dialog
        title="主机管理"
        :visible.sync="addShow"
        width="500px"
        :close-on-click-modal='false'
        >
        <el-form ref="ruleForm" :model="editData" :rules="rules" label-width="80px">
            <el-form-item label="主机名" prop="serverName">
                <el-input clearable  class="dialog_pram" placeholder="请输入主机名" v-model="editData.serverName"  />
            </el-form-item>
            <el-form-item label="地址" prop="serverIP">
                <el-input clearable class="dialog_pram" placeholder="请输入地址" v-model="editData.serverIP"  />
            </el-form-item>
            <el-form-item label="端口号" prop="serverHost">
                <el-input clearable  class="dialog_pram" placeholder="请输入端口号" v-model="editData.serverHost"  />
            </el-form-item>
            <el-form-item label="用户名" prop="userName">
                <el-input clearable class="dialog_pram" placeholder="请输入用户名" v-model="editData.userName" />
            </el-form-item>
            <el-form-item label="密码" prop="passWord">
                <el-input clearable class="dialog_pram" placeholder="请输入密码" v-model="editData.passWord" />
            </el-form-item>
            <el-form-item label="文件路径" prop="serverFilePath">
                <el-input clearable class="dialog_pram" placeholder="请输入文件路径" v-model="editData.serverFilePath" />
            </el-form-item>
            <el-form-item label="备注" >
                <el-input type="textarea" :rows="3" clearable class="dialog_pram" placeholder="请输入备注" v-model="editData.remark" />
            </el-form-item>
        </el-form>   
        <span slot="footer">
            <el-button  type="primary"  @click="submitUpload">确定</el-button>
            <el-button  type=""  @click="cancelUpload">取消</el-button>
        </span>
    </el-dialog>
    <div class="filter-container">
        <el-button class="filter-item" type="primary" icon="el-icon-edit" @click="updateData({})">新增主机</el-button>
    </div>

    <el-table
      v-loading="listLoading"
      :data="dataList"
      :stripe="true"
      border
      fit
      highlight-current-row
      style="width: 100%;"
    >
      <el-table-column width="150" label="主机名" prop="serverName" align="center" />
      <el-table-column  label="地址" prop="serverIP" align="center">
        <template slot-scope="scope">
          <el-tag type="info">{{ scope.row.serverIP }}</el-tag>
        </template>
      </el-table-column>
      <el-table-column width="100" label="端口号" prop="serverHost" align="center" />
      <el-table-column  label="用户名" prop="userName" align="center" />
      <el-table-column  label="密码" prop="passWord" align="center" />>
      <el-table-column width="150" label="操作" prop="" align="center" >
        <template slot-scope="scope">
            <el-button type="primary" size="mini" @click="updateData(scope.row)">修改</el-button>
            <el-button type="warning" size="mini" @click="deleteData(scope.row.id)">删除</el-button>
        </template>
      </el-table-column>
    </el-table>
  </div>
</template>

<script>
import { Version } from "@/api/version";
export default {
  name: "version",
  data() {
    return {
      addShow: false, //是否显示弹出框
      // 编辑参数
      editData: {
        serverName: "",
        serverIP: "",
        serverHost: "",
        userName: "",
        passWord: "",
        serverFilePath: "",
        remark: ""
      },
      listLoading: false,
      dataList: [],
      currentId: "",
      rules: {
        serverName: [
          { required: true, message: "请输入主机名", trigger: "change" }
        ],
        serverIP: [
          { required: true, message: "请输入地址", trigger: "change" }
        ],
        serverHost: [
          { required: true, message: "请输入端口号", trigger: "change" }
        ],
        userName: [
          { required: true, message: "请输入用户名", trigger: "change" }
        ],
        passWord: [
          { required: true, message: "请输入密码", trigger: "change" }
        ],
        serverFilePath: [
          { required: true, message: "请输入文件路径", trigger: "change" }
        ],
      }
    };
  },
  created() {
    this.getList();
    this.baseUrl = Version.addVersion.baseUrl;
  },
  methods: {
    /**
     * @description 获取初始化数据
     * @function getList
     * @return {type} {description}
     */
    getList() {
      this.listLoading = true;
      Version.serviceGet().then(response => {
        this.dataList = response.data;
        this.listLoading = false;
      });
    },
    /**
     * @description 新增或者更新数据
     * @function {updateData
     * @return {type} {description}
     */
    updateData(row) {
      this.currentId = row.id || null;
      this.addShow = true;
      this.editData = {
        serverName: row.serverName,
        serverIP: row.serverIP,
        serverHost: row.serverHost,
        userName: row.userName,
        passWord: row.passWord,
        serverFilePath: row.serverFilePath,
        remark: row.remark
      };
    },
    /**
     * @function submitUpload
     * @return {type} {description}
     */
    submitUpload() {
      this.$refs.ruleForm.validate(valid => {
        if (valid) {
            this.editData.serverHost-=0;
          if (!this.currentId) {
            Version.serviceInstall(this.editData).then(res => {
              this.$message({
                type: "success",
                message: "成功新增"
              });
              this.getList();
              this.addShow=false;
            });
          } else {
            Version.serviceUpdate({
                id:this.currentId,
                ...this.editData
            }).then(res => {
              this.$message({
                type: "success",
                message: "成功更新"
              });
              this.getList();
              this.addShow=false;
            });
          }
        } else {
          return false;
        }
      });
    },
    cancelUpload() {
      this.addShow = false;
    },
    /**
     * @description 删除版本
     * @function deleteData
     * @return {type} {description}
     */
    deleteData(id) {
      this.$confirm("确定删除该主机?", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning"
      })
        .then(() => {
          Version.serviceDelete({
            id,
          }).then(res => {
            this.$message({
              type: "success",
              message: "成功删除"
            });
            this.getList();
          });
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: "已取消删除"
          });
        });
    }
  }
};
</script>
